<template>
    <div class="black-bg" v-if="show">
        <div class="tutorial">
            <div v-for="(slide, index) in slides" v-show="currentSlideIndex === index" :key="slide.id">
                <h3>{{ slide.title }}</h3>
                <p>{{ slide.content }}</p>
                <img :src="slide.image" alt="Tutorial Image" :style="{ width: '100%', height: '100%' }"/>
            </div>
            <button class="prev" @click="prevSlide" >이전</button>
            <button class="next" @click="nextSlide" v-if="currentSlideIndex < slides.length - 1">다음</button>
            <button class="closetutorial" @click="closetutorial" v-if="currentSlideIndex < slides.length - 1">skip</button>
            <button class="start" @click="closetutorial" v-if="currentSlideIndex === slides.length - 1"> 탐험을 시작해봅시다! </button>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        show: {
                type: Boolean,
                required: true,
            },

    },
    data(){
        return{
            currentSlideIndex: 0,

            slides: [
                {image : require('@/assets/info.png')},
                {title: "낮잠자고 있는 꼬미 (npc 말풍선)"},
                {title: "꿈을 꾸는 꼬미"},
                {title: "어? 이상하다 금강초롱꽃이 몇 송이 밖에 안 남아있다!"},
                {title: "기후위기로 인한 산불, 온도는 2도나 상승 2050년 우리의 모습"},
                {title: "엉엉 우는 꼬미 내가 좋아하는 꽃들이 다 어디로 간거야!"},
                {title: "누군가 꼬미를 깨운다! 꿩!! 꿩!!"},
                {title: "화들짝 놀란 꼬미 왜 놀랐는지 설명해주자"},
                {title: "그건 꿈일 뿐이야 꿩이 예나 지금이나 앞으로도 아름다운 국립공원"},
                {title: "같이 탐방하며 미션이 시작됨"}
            ],
        }
    },
    methods: {
        nextSlide() {
            if (this.currentSlideIndex < this.slides.length - 1) {
                this.currentSlideIndex++;
            }
        },
        prevSlide() {
            if(this.currentSlideIndex > 0){
                this.currentSlideIndex--;
            }
        },
        closetutorial(){
            this.currentSlideIndex = 0;
            this.$emit('closeTutorial');
        },
        showTutorialPopup(){
            this.currentSlideIndex = 0;
            this.$emit('openTutorial');
        },
    },
}
</script>

<style>
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(255, 255, 255, 0.1);
        position: fixed; padding: 20px;
        z-index: 1000;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .tutorial{
        position: relative;
        width: 90%;
        height: 90%;
        z-index: 1000;
        background: white;
        text-align: center;
        border-radius: 8px;
        justify-content: center;
        padding: 10px;
        display: flex;
        align-items: center;

        flex-direction: column;
    }
    .prev{
        position: absolute;
        left: 10px;
        bottom: 50%;
        background-color: #ccc;
        color: white;
        border-radius: 4px;
        border: none;
        padding: 5px 10px;
    }
    .next{
        position: absolute;
        right: 10px;
        bottom: 50%;
        background-color: #ccc;
        color: white;
        border-radius: 4px;
        border: none;
        padding: 5px 10px;
    }
    .closetutorial{
        position: absolute;
        top: 15px;
        right: 25px;
        display: flex;
        width: auto;
        padding: 5px 10px;
        margin-top: 5px;
        background-color: #ccc;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        margin-left: auto; /* 왼쪽 마진을 오토로 설정해서 오른쪽으로 밀어냄 */
    }
    .start{
        position: absolute;
        background-color: blue;
        color: white;
        border: none;
        border-radius: 4px;
        padding: 10px 20px;
        bottom: 20%;
    }
</style>