<template>
  <div class="black-bg" v-if="show">
    <div class="white-bg"> 
        <h4> 모은 두루마리 예아~ </h4>
        <button @click="closeP">닫기</button>
        <div class="durumari">
          <div class="d1">
            <img :src="getDurumariImage(1)" :style="{width:'70%', height:'70%'}">
          </div>
          <div class="d2">
            <img :src="getDurumariImage(2)" :style="{width:'70%', height:'70%'}">
          </div>
          <div class="d3">
            <img :src="getDurumariImage(3)" :style="{width:'70%', height:'70%'}">
          </div>
          <div class="d4">
            <img :src="getDurumariImage(4)" :style="{width:'70%', height:'70%'}">
          </div>
          <div class="d5">
            <img :src="getDurumariImage(5)" :style="{width:'70%', height:'70%'}">
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import EventBus from '@/EventBus.js'

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
    },

    methods: {
      closeP(){
          this.$emit('close');
      },
      getDurumariImage(index){
        switch (index) {
          case 1:
          return this.r1 ? require('@/assets/durumari1.png') : require('@/assets/durumariQ.png');
          case 2:
          return this.r2 ? require('@/assets/durumari2.png') : require('@/assets/durumariQ.png');
          case 3:
          return this.r3 ? require('@/assets/durumari3.png') : require('@/assets/durumariQ.png');
          case 4:
          return this.r4 ? require('@/assets/durumari4.png') : require('@/assets/durumariQ.png');
          case 5:
          return this.r5 ? require('@/assets/durumari5.png') : require('@/assets/durumariQ.png');
        }
      },
    },
    created(){
      EventBus.$on('send-results-to-durumari', (res) => {
        this.r1 = res.r1;
        this.r2 = res.r2;
        this.r3 = res.r3;
        this.r4 = res.r4;
        this.r5 = res.r5;
      })
    },
    data(){
      return{
        r1: null,
        r2: null,
        r3: null,
        r4: null,
        r5: null,
      }
    }
}
</script>

<style>
    body{
        margin: 0;
    }
    div{
        box-sizing: border-box;
    }
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; padding: 20px;
        z-index: 1000;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .white-bg{
        width: 80%; height: 80%; 
        background:white;
        border-radius: 8px;
        padding: 50px;
    }
    .d1{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .d2{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .d3{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .d4{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .d5{
      display: flex;
      justify-content: center;
      align-items: center;
    }
</style>