<template>
    <div class="black-bg" v-if="show">
      <div class="white-bg"> 
        <button class="close" @click="closeP">X</button> 
          <h4> 환경 퀴즈 </h4>
          <p> 0000이란 지구온난화를 막기 위해서 탄소배출은 최소화 하고 </p>
          <p> 발생한 탄소는 다시 흡수하여 탄소배출량이 '0'이 되게 하는 거에요. </p>
          <p> 파리기후협약에서 합의한 대로 2050년까지 지구 상승온도를 1.5도를 제한하기 위해서 </p>
          <p> 우리나라도 '2050 0000' 정책을 펼치고 있어요 </p>
          <p> 0000은 무엇일까요? </p>
          <br>
          <br>
          <br>
          <input type="text" v-model="userAnswer">
          <div class="button-container">
            <button class="submit" @click="checkAnswer">제출하기</button>
            <button class="hint" @click="showHint">예시</button>
        </div>
        <div class="hint-popup" v-if="showPopup">
                <button class="close-popup" @click="closePopup">X</button>
                <p> 힌트 팝업 페이지 </p>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
      data(){
        return{
            showPopup:false,
            userAnswer:'',
        };
      },
      props: {
          show: {
              type: Boolean,
              required: true,
          },
          result5: {
            type: Boolean
          }
      },
  
      methods: {
        closeP(){
              this.$emit('close');
          },
          //팝업 여는 메소드
        showHint(){
            this.showPopup=true;
          },
          //팝업 닫는 메소드
        closePopup(){
            this.showPopup=false;
          },
          checkAnswer(){
            if(this.userAnswer === '탄소중립'){
                alert('정답입니다!');

                if(this.result5 == false) {
                    this.$emit('answerCorrect')
                }

                // 팝업 닫기
                this.closeP();
            } else{
                alert('오답입니다. 다시 시도해보세요');
            }
          }
      }
  }
  </script>
  
  <style>
      body{
          margin: 0;
      }
      div{
          box-sizing: border-box;
      }
      .black-bg{
          width: 100%; height: 100%;
          background: rgba(0, 0, 0, 0.5);
          position: fixed; padding: 20px;
          z-index: 1000;
          display: flex;
          align-items: center;
          justify-content: center;
      }
      .white-bg{
          width: 80%; height: 80%; 
          background:white;
          border-radius: 8px;
          padding: 50px;
      }
      .close-button{
        position: absolute;
        top: 20px; /* 위로 10px 이동 */
        right: 20px; /* 오른쪽으로 10px 이동 */
      }
  </style>