<template>
  <div class="main">    
    <l-map
      style="height: 100%; width: 100%"
      :center="center"
      :zoom="zoom"
      :zoomAnimation=true
      :options="mapOptions"
      class="map"
      ref="map"
      @update:zoom="zoomUpdated"
      @update:center="centerUpdated"
      @update:bounds="boundsUpdated"
    >
      <pop1 :show="popVal1" @close="closeP" @answerCorrect="updateResult(1)" class="popup" :class="{'show':popVal1}" />
      <pop2 :show="popVal2" @close="closeP" @answerCorrect="updateResult(2)" class="popup" :class="{'show':popVal2}" />
      <pop3 :show="popVal3" @close="closeP" @answerCorrect="updateResult(3)" class="popup" :class="{'show':popVal3}" />
      <pop4 :show="popVal4" @close="closeP" @answerCorrect="updateResult(4)" class="popup" :class="{'show':popVal4}" />
      <pop5 :show="popVal5" @close="closeP" @answerCorrect="updateResult(5)" class="popup" :class="{'show':popVal5}" />

      <LMarker
        :key="markers[0].id"
        :lat-lng="markers[0].coordinates"
        @click="openP(0)"
        :icon="result1 ? customIcon: defaultIcon" />

      <LMarker
        :key="markers[1].id"
        :lat-lng="markers[1].coordinates"
        @click="openP(1)"
        :icon="result2 ? customIcon: defaultIcon" />

      <LMarker
        :key="markers[2].id"
        :lat-lng="markers[2].coordinates"
        @click="openP(2)"
        :icon="result3 ? customIcon: defaultIcon" />

      <LMarker
        :key="markers[3].id"
        :lat-lng="markers[3].coordinates"
        @click="openP(3)"
        :icon="result4 ? customIcon: defaultIcon"></LMarker>

      <LMarker
        :key="markers[4].id"
        :lat-lng="markers[4].coordinates"
        @click="openP(4)"
        :icon="result5 ? customIcon: defaultIcon" />

      <l-tile-layer :url="url" :attribution="attribution"/>

      <input type="button" value="내 위치" @click="ZoomInToCurrentPosition" :class="{'location-button' : !isZoom, 'location-button-red' : isZoom }" class="my-location-button">
      <input type="button" value="탐방로" @click="goToCenter" class="center-button">

      <l-control-zoom position="bottomleft"></l-control-zoom>

    </l-map>
  </div>
</template>
   
<script>
import { LMap, LTileLayer, LControlZoom, LMarker } from 'vue2-leaflet';
import L from 'leaflet';
import { Icon } from 'leaflet';

import EventBus from '@/EventBus.js';

import pop1 from '../components/popView1.vue';
import pop2 from '../components/popView2.vue';
import pop3 from '../components/popView3.vue';
import pop4 from '../components/popView4.vue';
import pop5 from '../components/popView5.vue';

import 'leaflet/dist/leaflet.css';
import 'leaflet-gpx';

import markerImg from 'leaflet/dist/images/marker-icon.png';
import markerShadowImg from 'leaflet/dist/images/marker-shadow.png';
import markerRetinaImg from 'leaflet/dist/images/marker-icon-2x.png';

export default {
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      // center: [ 37.40436362096728, 128.0495492913938], // 처음 지도의 중심이 되는 좌표 / 각 포인트들의 중점
      // center: [37.4148911042466, 128.05010676384],
      center:[37.405103579156, 128.04869055748],
      zoom: 15, // 초기 확대 레벨
      previousZoom : null, //현재의 확대 레벨
      bounds: null, //지도 경계가 변경될 때 발생하는 이벤트(드래그되거나 확대/축소 시 발생)

      popVal1 : false, //pop1이 열려있나 닫혀있나 여부 저장(default=false)
      popVal2 : false,
      popVal3 : false,
      popVal4 : false,
      popVal5 : false,

      result1: false,
      result2: false,
      result3: false,
      result4: false,
      result5: false,

      isZoomIn: false, //줌인이 된 상태인지 아닌지 확인(팝업창 띄우기 위한 변수)
      isZoom : false, //내 위치 버튼을 위한 줌인 상태 확인 변수
      current : [0,0], // ZoomInToCurrentPosition 메서드에서 버튼 누른 시점의 위치 저장을 위한 변수

      isPositionReady : false,
      positionObj: {
        latitude: 0,
        longitude: 0
      },

      // marker: null,

      mapOptions: {
        minZoom: 13, //최소 줌 레벨 설정
        maxZoom: 18,  //최대 줌 레벨 설정
        zoomControl: false, // 줌 컨트롤러 위치를 바꿔줄 예정(왼쪽 상단 -> 왼쪽 하단)
      },
      markers: [
        {id:1, coordinates:[37.4148911042466, 128.05010676384], name:'치악산국립공원사무소', imageUrl: require('@/assets/ggomi.png')},
        {id:2, coordinates:[37.4079926725627, 128.045756220818], name:'구룡자동차야영장', imageUrl: require('@/assets/ggomi.png')},
        {id:3, coordinates:[37.405103579156, 128.04869055748], name:'황장숲목길', imageUrl: require('@/assets/ggomi.png')},
        {id:4, coordinates:[37.3992895011957, 128.04955099951], name:'구룡사', imageUrl: require('@/assets/ggomi.png')},
        {id:5, coordinates:[37.3945412476754, 128.053641915321], name:'자생식물관찰원', imageUrl: require('@/assets/ggomi.png')},
      ],
      defaultIcon: new Icon({  // 지도의 마커 사용자 지정 아이콘(기본 디폴트 아이콘)
        iconUrl: require('@/assets/ggomi.png'),
        iconSize: [50, 50],
        iconAnchor: [16,32]
      }),
      customIcon: new Icon({  // 정답 시 바뀔 아이콘
        iconUrl: require('@/assets/꼬미도장.png'),
        iconSize: [50, 50],
        iconAnchor: [16,32]
      }),
    };
  },
  components: {
    LMap,
    LTileLayer,
    LControlZoom,
    LMarker,
    pop1,
    pop2,
    pop3,
    pop4,
    pop5,
  },
  mounted(){

    delete Icon.Default.prototype._getIconUrl;
    Icon.Default.mergeOptions({ //marker-icon-2x.png 이미지 파일을 못찾는 에러 제거 코드
      iconRetinaUrl: markerRetinaImg,
      iconUrl: markerImg,
      shadowUrl: markerShadowImg,
    });

    // gpx 파일을 위한 코드
    this.loadGPX();

    // 현재 위치 불러오는 코드
    this.getCurrentPosition();
    this.currentPosIcon = L.icon({
      iconUrl: require('@/assets/logo.png'),
      iconSize: [16,16],
      iconAnchor: [16,32]
    });

    // this.$emit('send-r1', this.result1);
    // this.$emit('send-r2', this.result2);
    // this.$emit('send-r3', this.result3);
    // this.$emit('send-r4', this.result4);
    // this.$emit('send-r5', this.result5);

  },
  methods: {
    zoomUpdated (zoom) {
      this.zoom = zoom;
    },
    centerUpdated (center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    openP(index){
      if(! this.isZoomIn){
        this.previousZoom = this.zoom;
        // 먼저 줌인 액션 실행
        const marker = this.markers[index];
        this.$refs.map.mapObject.setZoomAround(marker.coordinates, 36);
        this.isZoomIn = true;
      }
      // 0.8초 뒤 팝업 열기
      setTimeout(()=>{
        this['popVal' + (index+1)] = true;
      }, 800); 
    },
    closeP(){
      this.popVal1 = false;
      this.popVal2 = false;
      this.popVal3 = false;
      this.popVal4 = false;
      this.popVal5 = false;

      if (this.isZoomIn) {
        // 팝업이 닫힌 후 줌 아웃(원래 줌 레벨로 돌아가기)
        this.$refs.map.mapObject.setZoom(this.previousZoom);
        this.isZoomIn = false;
      }
    }, 

    getPositionValue(pos) {
      this.positionObj.latitude = pos.coords.latitude;
      this.positionObj.longitude = pos.coords.longitude;
      this.currentPos = [this.positionObj.latitude, this.positionObj.longitude];
      this.isPositionReady = true;
       
      if (this.$refs.map && this.$refs.map.mapObject) {
        // 이컨 마커가 있다면 삭제 (안하면 위치 변동될때마다 계속 마커가 찍힘)
        if(this.marker){
          this.$refs.map.mapObject.removeLayer(this.marker)
        }
        // 새 마커 추가
        this.marker = L.marker(this.currentPos, {icon: this.currentPosIcon}).addTo(this.$refs.map.mapObject);
      }
    },
    getCurrentPosition(){
      if(!navigator.geolocation){
        alert('위치 정보를 찾을 수 없습니다.')
      } else {
        navigator.geolocation.watchPosition(this.getPositionValue, 
        ()=>alert('위치 정보를 찾을 수 없습니다.2')) //위치 업데이트 시 호출되는 함수 getPositionValue, 오류 발생 시 경고메세지 표시
      }
    },
    FixedCurrentPosition() {
      this.$refs.map.mapObject.setView(this.currentPos, this.zoom);
        this.isZoom = false;
    }, // 토글로 바꿔야겠다

    ZoomInToCurrentPosition(){
      if(this.isZoom) {
        // 버튼을 한번 더 누르면 실시간 현재 내 위치가 지도의 중심으로 고정
        this.$refs.map.mapObject.setView(this.currentPos, this.zoom);
        this.isZoom = false;
      } else {
        this.current = this.currentPos; //currentPos는 실시간으로 변하는 위치를 감지하기 때문에 변하지 않도록 다른 변수에 현재 위치 저장
        this.$refs.map.mapObject.setView(this.current, 16);
        this.isZoom = true;
      } 
    },

    goToCenter(){
      this.$refs.map.mapObject.setView([37.408473, 128.045787],14);
    },
    loadGPX(){
      //.gpx 파일 로드 및 지도에 표시
      const gpxUrl = process.env.BASE_URL + '치악산 탐방서비스 등산로.gpx'
      fetch(gpxUrl)
      .then(response => response.text())
      .then(data => {
        // GPX 트랙 파싱
        const gpx = new DOMParser().parseFromString(data, 'text/xml');
        const trackCoords = [];
        
        // 트랙 세그먼트 추출
        const trkseg = gpx.getElementsByTagName('trkpt');
        for (let i = 0; i < trkseg.length; i++) {
          const lat = parseFloat(trkseg[i].getAttribute('lat'));
          const lon = parseFloat(trkseg[i].getAttribute('lon'));
          trackCoords.push([lat, lon]);
        }
        
        // 새로운 폴리선(선) 생성
        const polyline = L.polyline(trackCoords, { color: 'green', weight: 15 });

        // 맵에 폴리선 추가
        polyline.addTo(this.$refs.map.mapObject);

        // 지도 중심과 확대 설정 (트랙에 맞게 조절)
        // this.$refs.map.mapObject.fitBounds(polyline.getBounds());
        this.$refs.map.mapObject.setView(this.center, this.zoom);
      })
      .catch(error => {
        console.error('Error loading GPX:', error);
      });
    },

    updateResult(id){
      this['result' + (id)]=true;
      const results = {
        r1: this.result1,
        r2: this.result2,
        r3: this.result3,
        r4: this.result4,
        r5: this.result5,
      };
      EventBus.$emit('send-results-to-durumari', results);
    }
  }
}
</script>

<style>
  .map {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow :hidden
  }
  .marker-ggomi-icon {
    height: 50px;
    width: auto;
  }
  .icon{
    height:50px;
    width: 50px;
  }
  .popup{
    /* 트랜지션 속성을 추가하여 부드러운 나타남 및 사라짐 만들기 */
    transition: opacity 0.5s; 
    opacity: 0; /*초기에 투명상태로 설정*/
  }
  .popup.show{
    opacity: 1; /*나타날 때 투명도를 1로 설정하여 부드럽게 나타나게 함*/
  } 
  .my-location-button {
    z-index: 1001;
    position: absolute;
    bottom: 150px;
  }
  .center-button {
    z-index: 1001;
    position: absolute;
    bottom: 100px;
  }
  .location-button-red {
    background-color: red;
    color: white;
  }
</style>